
$body:#000;

.home{
    display: block;
    height: max-content;

    .banner{
        background: $body;
        background-image: url(./Components/images/banner.jpg);
        background-position: center center;
        background-repeat: no-repeat;
        height: calc( 100vh - 75px);
        position: relative;
        display: flex;
        justify-content: center;
        flex-direction: column;
    
        &::after{
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            opacity: 0.4;
            background: $body;
        }
        .button_box{
            width: 50%;
            display: flex;
            flex-direction: column;
            z-index: 10;
            padding-left:2rem ;
    
            @media only screen and (max-width: 991px){
                &{
                    width: 100%; 
                }
            }
            @media only screen and (max-width: 691px){
                &{
                    align-items: center; 
                    padding-left:0rem ;
                }
            }
            h1,h2{
                margin-bottom: 1rem;
                @media only screen and (max-width: 991px){
                    &{
                        word-wrap:break-word; 
                    }
                }
                @media only screen and (max-width: 691px){
                    &{
                        text-align:center; 
                    }
                }
            }
            h1{
                color: #fff;
                font-size: 4rem;
                font-weight: bolder;
                @media only screen and (max-width: 991px){
                    &{
                        font-size: 2.3rem;
                    }
                }
            }
            h2{
                color: #ffffff96;
                font-size: 1.5rem;
                font-weight: normal;
                @media only screen and (max-width: 991px){
                    &{
                        font-size: 1.3rem;
                    }
                }
            }
            div{
                display: flex;
                align-items: center;
                @media only screen and (max-width: 691px){
                    &{
                        padding-left: 2rem;
                    }
                }
    
                button{
                    color: $body;
                    width:auto;
                    flex-wrap: wrap;
                    padding: 10px 15px;
                    margin-right: 2rem;
                    border-radius: 5px;
                    font-weight: bold;
                    border: none;
                    cursor: pointer;
                    transition: all .3s;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    svg{
                        color: #000 !important;
                        padding-right: 4px;
                        font-size: 1.3rem;
                    }
                    &:hover{
                        background-color: #464545af;
                        color: #fff !important;  
                     svg{
                            color: #fff !important;
                        } 
                    }                
                    @media only screen and (max-width: 991px){
                        &{
                            width:auto;
                        }
                    }
    
                }
            }
        }
    }
}

.row{
    padding: 1rem;
   h2{
      font-size: 1.6rem;
      margin: 0.3rem;
      color: #fff;
   } 
.card{
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
    margin-top: 2rem;
    position: relative;
    
  &::-webkit-scrollbar{
      appearance: none;
  }
    .listItem{
        // max-width: 200px;
        width: 100%;
        height: 100%;
        cursor: pointer;
        position: relative;
       
        .card_img{
            width: 250px;
            height: 100%;
            object-fit: cover;
            margin: 0 2px;
            // width: 200px;
            // cursor: pointer;
            // transition: all .5s;
            // margin: 0.6rem;
            // border-radius: 5px;     
        // &:hover{
        //         opacity: 0.6;
        //     }
        }
        &:hover{ 

            .hover{
                position: absolute;
                z-index: 90;
                height:max-content;
                padding-bottom: 2rem;
                width: 100%;
                top: 0vh;
                left: 0px;
                border-radius: 0.3rem;
                background-color: #000;
                color: #fff;
                transition: all 0.3s ease-in-out 0s;
                box-shadow: #3f3e3ec7 0px 0px 10px;

                .image-video-container {
                    position: relative;
                    height: 200px;
                    // background: #31313123;

                    video {
                        width: 250px;
                        height: 200px;
                        margin: 0 2px;
                        object-fit: cover;
                        border-radius: 0.3rem;
                        top: 0px;
                        z-index: 5;
                        position: absolute;
                    }
                }
                .info-container {
                    padding: 1rem;
                    gap: 0.5rem;
                    flex-direction: column;
                    display: flex;
                    overflow-y: hidden;
                    padding-bottom: 0;
                    // max-height: 200px;

                    p{
                        font-size: 14px;
                        overflow: hidden;
                        white-space: nowrap;
                        text-overflow: ellipsis;
                        // max-width: 200px;
                        max-width: 75ch;

                    }
                }
            }

            video{
                width: 100%;
                height: 140px;
                object-fit: cover;
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }

  }
}